import countries from '../country'

export default {
    title: 'Customers',
    icon: 'mdi-account-box',
    // import: true,
    // export: true,
    filters:[
        {
            input: 'text',
            name: 'tpc',
            bind: {
                label: "TP Account",
                prependIcon: 'mdi-card-account-details',
             
            }
        },
        {
            input: 'text',
            name: 'name',
            bind: {
                label: "Account Name",
                prependIcon: 'mdi-account-box',
            
            }
        },
        {
            input: 'autocomplete',
            name: 'country',
            bind: {
                label: "Country",
                prependIcon: "mdi-flag",
                items: countries,
                itemText: "name",
                itemValue: "code",
                hideAddNew: true,
                
            }
        },
        {
            input: 'autocomplete',
            name: 'affiliate',
            bind: {
                label: "Affiliate",
                collection: 'affiliate',
                itemText: "name",
                itemValue: "_id",
                prependIcon: "mdi-account-network-outline",
            }
        },
        {
            input: 'autocomplete',
            name: 'user',
            if: state => (state.auth.user.rights.filter(e => e.collection == 'customer')[0].write.allOwn == 'all'),

            bind: {

                label: "Owner",
                prependIcon: "mdi-account",
                collection: 'user',
                itemText: "displayName",
                itemValue: "_id",
                hideAddNew: true,
                query: {
                    role: "!wakel",
                    affiliate: "!"
                }
            }
        },
        {
            input: 'select',
            name: 'status',
            bind: {
                label: "Status",
                items: [
                    { text: 'All', value: null },
                    { text: 'New', value: 'new' },
                    { text: 'Call again', value: 'call again' },
                    { text: 'No Answer', value: 'no Answer' },
                    { text: 'Not interested', value: 'not interested' },
                ],
                itemText: "text",
                itemValue: "value",
                prependIcon: "mdi-list-status",
            }
        }
    ],
    table: [
        {
            text: 'TP Account',
            align: 'start',
            value: 'tpc',
        },
        { text: 'Account name', value: 'name' },
        { text: 'Country', value: 'country' },
        { text: 'Owner', value: 'user.displayName' },
        { text: 'Status', value: 'status' },
        { text: 'Affiliate', value: 'affiliate.name' },
        // { text: 'FTD', value: 'ftd[0].amount' },
        // { text: 'FTD Date', value: 'ftd[0].created_on', width: 120 },
        // { text: 'Deposits', value: 'child.length' },
        // { text: 'Total Deposits', value: 'total[0].amount' },
        { text: 'Actions', value: 'actions', width: '100' }

    ],
    // expanded: {
    //     headers: [
    //         {
    //             text: 'Date',
    //             align: 'start',
    //             value: 'created_on',
    //         },
    //         { text: 'Amount', value: 'amount' },
            
    //     ],
    // },
    form: [
        {
            label: 'General',
            fields: [
                {
                    input: 'text',
                    name: 'tpc',
                    bind: {
                        label: "TP Account",
                        prependIcon: 'mdi-card-account-details',
                        required: true,
                        rules: [
                            v => !!v || 'ID is required',
                        ],
                    }
                },
                {
                    input: 'text',
                    name: 'name',
                    bind: {
                        label: "Account Name",
                        prependIcon: 'mdi-account-box',
                        required: true,
                        rules: [
                            v => !!v || 'ID is required',
                        ],
                    }
                },
                {
                    input: 'autocomplete',
                    name: 'country',
                    bind: {
                        label: "Country",
                        prependIcon: "mdi-flag",
                        items: countries,
                        itemText: "name",
                        itemValue: "code",
                        required: true,
                        hideAddNew: true,
                        rules: [
                            v => !!v || 'Country is required',
                        ],
                    }
                },
                {
                    input: 'autocomplete',
                    name: 'affiliate',
                    bind: {
                        label: "Affiliate",
                        collection: 'affiliate',
                        itemText: "name",
                        itemValue: "_id",
                        prependIcon: "mdi-account-network-outline",
                    }
                },
                {
                    input: 'autocomplete',
                    name: 'user',
                    if: state => (state.auth.user.rights.filter(e => e.collection == 'customer')[0].write.allOwn == 'all'),
                    default: state => state.auth.user._id,
                    bind: {
                       
                        label: "Owner",
                        prependIcon: "mdi-account",
                        collection: 'user',
                        itemText: "displayName",
                        itemValue: "_id",
                        hideAddNew: true,
                    }
                },
                {
                    input: 'select',
                    name: 'status',
                    default: () => 'new',
                    bind: {
                        label: "Status",
                        items: [
                            { text: 'New', value: 'new' },
                            { text: 'Call again', value: 'call again' },
                            { text: 'No Answer', value: 'no Answer' },
                            { text: 'Not interested', value: 'not interested' },
                        ],
                        itemText: "text",
                        itemValue: "value",
                        prependIcon: "mdi-list-status",
                    }
                }
            ]
        }
    ]
}