




export default {
    title: 'Deposits',
    icon: 'mdi-bank-transfer-in',
    withdraw: true,
    filters:[
       
        {
            input: 'autocomplete',
            name: 'business',
            if: state => !(state.auth.user.business),
            bind: {
                label: "Business Unit",
                prependInnerIcon: "mdi-domain",
                collection: 'business',
                itemText: "name",
                itemValue: "_id",
                hideAddNew: true,
               
            }
        },
        {
            input: 'autocomplete',
            name: 'department',
            if: state => !(state.auth.user.department),
            bind: {
                label: "Department",
                prependInnerIcon: "mdi-briefcase-account-outline",
                collection: 'department',
                itemText: "name",
                itemValue: "_id",
                hideAddNew: true,
               
                
            },
            on: {
                input(evt) {
                    const { user } = window.$items.$refs
                    if (!evt && user[0].query.department)
                        delete user[0].query.department
                    else if(evt) 
                        user[0].query.department = evt
                   
                }
            }
        },
        {
            input: 'autocomplete',
            name: 'user',
            if: state => (state.auth.user.rights.filter(e => e.collection == 'deposit')[0].write.allOwn == 'all'),
            bind: {
                label: "Owner",
                prependInnerIcon: "mdi-account",
                collection: 'user',
                itemText: "displayName",
                itemValue: "_id",
                hideAddNew: true,
                or: false,
                query: {
                    affiliate: '!',
                    role: '!wakel'
                }
                
            }
        },
        {
            input: 'autocomplete',
            name: 'customer',
            bind: {
                label: "Account",
                prependInnerIcon: "mdi-account-box",
                collection: 'customer',
                itemText: "name",
                itemValue: "_id",
                searchFields: ['name', 'tpc'],
                hideAddNew: true,

            }
        },
        {
            input: 'range',
            name: 'created_on',
            bind: {
                label: "Date",
                prependInnerIcon: "mdi-calendar",

            }
        }

    ],
    table: [
        {
            text: 'TP Account',
            align: 'start',
            value: 'customer.tpc',
        },
        { text: 'Account name', value: 'customer.name' },
        { text: 'Type', value: 'type' },
        { text: 'Amount', value: 'amount' },
        // { text: 'Email', value: 'email' },
        { text: 'Payment Method', value: 'payment_method.name' },
        { text: 'Affiliate', value: 'customer.affiliate.name' },
        { text: 'Business Unit', value: 'business.name' },
        { text: 'Department', value: 'department.name' },
        { text: 'Date', value: 'created_on' },
        { text: 'Owner', value: 'user.displayName' },
        { text: 'Actions', value: 'actions', width: '150' }

    ],
    form: [
        {
            label: 'General',
            fields: [
                {
                    input: 'autocomplete',
                    name: 'customer',
                    bind: {
                        label: "Account",
                        prependIcon: "mdi-account-box",
                        collection: 'customer',
                        itemText: "name",
                        itemValue: "_id",
                        required: true,
                        searchFields: ['name', 'tpc'],

                        rules: [
                            v => !!v || 'Account is required',
                        ],
                    }
                },
                {
                    input: 'text',
                    name: 'amount',
                   
                    bind: {
                        label: "Amount",
                        prependIcon: 'mdi-cash',
                        required: true,
                        type: 'number',
                        rules: [
                            v => !!v || 'Amount is required',
                        ],
                    }
                },
               
                {
                    input: 'autocomplete',
                    name: 'payment_method',
                    if() {
                        // hide on withdraw
                        const urlParams = new URLSearchParams(location.search);
                        return urlParams.get('type') != 'withdraw'
                    },
                    default: () =>  null,
                    bind: {
                        label: "Payment Method",
                        prependIcon: "mdi-credit-card-settings-outline",
                        collection: 'payment_method',
                        itemText: "name",
                        itemValue: "_id",
                        required: true,
                        
                        
                        rules: [
                            v => !!v || 'Payment Method is required',
                        ],
                    }
                },
                {
                    input: 'hidden',
                    name: 'type',
                    default () {
                        const urlParams = new URLSearchParams(location.search);
                        return urlParams.get('type') || 'deposit'
                    }
                },
                {
                    input: 'date',
                    name: 'created_on',
                    default: (new Date()).toISOString().split('T')[0],
                    bind: {
                        label: "Created Date",
                        prependIcon: 'mdi-calendar',
                        required: true,
                        
                        
                        rules: [
                            v => !!v || 'Created On is required',
                        ],
                    }
                },

                {
                    input: 'autocomplete',
                    name: 'business',
                    if: state => !(state.auth.user.business),
                    default: state => state.auth.user.business,
                    bind: {
                        label: "Business Unit",
                        prependIcon: "mdi-domain",
                        collection: 'business',
                        itemText: "name",
                        itemValue: "_id",
                        required: true,
                        rules: [
                            v => !!v || 'Business Unit is required',
                        ],
                    }
                },
                {
                    input: 'autocomplete',
                    name: 'department',
                    if: state => !(state.auth.user.department),
                    default: state => state.auth.user.department,
                    bind: {
                        label: "Department",
                        prependIcon: "mdi-briefcase-account-outline",
                        collection: 'department',
                        itemText: "name",
                        itemValue: "_id",
                        required: true,
                        rules: [
                            v => !!v || 'Department is required',
                        ],
                    },
                    on: {
                        input(evt) {
                            const { deposit_user } = window.$form.$refs
                            if (!evt && deposit_user[0].query.department)
                                delete deposit_user[0].query.department
                            else if (evt)
                                deposit_user[0].query.department = evt

                        }
                    }
                },
                
                {
                    input: 'autocomplete',
                    name: 'user',
                    if: state => (state.auth.user.rights.filter(e => e.collection == 'deposit')[0].write.allOwn == 'all'),
                    default: state => state.auth.user._id,
                    bind: {
                        label: "Owner",
                        prependIcon: "mdi-account",
                        collection: 'user',
                        itemText: "displayName",
                        itemValue: "_id",
                        required: true,
                        rules: [
                            v => !!v || 'Owner is required',
                        ],
                        // or: false,
                        // query: {
                        //     affiliate: '!',
                        //     role: '!wakel'
                        // }
                    }
                },
                
               
            ]
        }
    ]
}